    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <van-datetime-picker
            v-model="currentDate"
            type="year-month"
            :min-date="minDate"
            :formatter="formatter"
            @confirm="confirmTime"
            @cancel="closeTime"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>

    <mu-container>
      <mu-bottom-sheet :open.sync="open1">
        <mu-list @item-click="closeBottomSheet1">
          <van-datetime-picker
            v-model="DateTime"
            type="date"
            :min-date="minDate"
            @confirm="confirmDateTime"
            @cancel="closeDateTime"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <!-- <van-action-sheet v-model="showoptions" title="选择角色">
      <p>内容</p>
    </van-action-sheet>-->
    <!-- <van-action-sheet v-model="showoptionsgender" title="选择性别"></van-action-sheet> -->
    <van-popup v-model="showoptionsgender" position="bottom">
      <van-picker
        show-toolbar
