<script>
import AppBace from "../../../static/ajax.js";
import Vue from "vue";
import { Toast } from "vant";
import { Picker } from "vant";
import { Dialog, Loading } from "vant";
Vue.use(Toast).use(Picker);
// import Vue from 'vue';
import { Popup, Overlay } from "vant";
Vue.use(Popup)
  .use(Overlay)
  .use(Loading);
export default {
  name: "register",
  data() {
    return {
      showover: false,
      value3: new Date(),
      options: ["管理员", "环保管理部门", "运维人员"],
      optionsgender: ["男", "女"],
      normal: {
        value1: "运维人员"
      },
      dialogImageUrl: "",
      dialogVisible: false,
      formDate: {},
      input: {
        birth: "",
        gender: "男",
        identity: "",
        phone: "",
        profession: "",
        roleId: "运维人员",
        school: "",
        username: "",
        technicalTitle: "",
        experienceTemp: ""
      },
      experienceTemp: {
        //从业经历
        innerMap: [
          {
            startTime: "",
            endTime: "",
            company: ""
          }
        ]
      },
      open: false,
      currentDate: new Date(),
      minDate: new Date("1970-01-01"),
      DateTime: new Date(),
      open1: false,
      show: "",
      index: 0,
      showoptions: false,
      showoptionsgender: false,
      enterprise: ""
    };
  },
  mounted() {},
  methods: {
    submitbefore() {
      var _this = this;
      Dialog.confirm({
        title: "提交",
        message: "请确认信息是否完善？"
      })
        .then(() => {
          // on confirm
          _this.submit();
          _this.showover = true;
        })
        .catch(() => {
          // on cancel
        });
    },
    selectDateTime(e, index) {
      this.open1 = true;
      this.show = e;
      this.index = index;
    },
    confirmDateTime(time) {
      this.open1 = false;
      var index = this.index;
      // console.log(AppBace.filter(time));
      switch (this.show) {
        case "start":
          this.experienceTemp.innerMap[index].startTime = AppBace.filter(time);
          break;

        case "end":
          this.experienceTemp.innerMap[index].endTime = AppBace.filter(time);
          break;
      }
    },
    closeDateTime() {
      this.open1 = false;
    },
    closeBottomSheet1() {
      this.open1 = false;
    },
    confirmTime(time) {
      // console.log(time);
      // console.log(AppBace.filterDate(time));
      this.input.birth = AppBace.filterDate(time);
      this.open = false;
    },
    closeTime() {
      this.open = false;
      this.input.birth = "";
    },
    formatter(type, value) {
      if (type === "year") {
        return `${value}年`;
      } else if (type === "month") {
        return `${value}月`;
      }
      return value;
    },
    closeBottomSheet() {
      this.open = false;
    },
    selectTime() {
      // this.$refs.pickerTime.show();
      this.open = true;
    },
    removeIndex(index) {
      this.experienceTemp.innerMap.splice(index, 1);
    },
    addToCompeny(e) {
      var arr = [];
      e.push({
        startTime: "",
        endTime: "",
        company: ""
      });
    },
    backTo() {
      this.$router.go(-1);
    },
    limitStatus() {},
    uploadFile(res) {
      // this.formDate = new FormData();
      this.formDate.append("photos", res.file);
      console.log(res.file);
    },
    handleRemove(file, fileList) {
      console.log("filelist", fileList);
    },
    init(e) {
      for (let index = 0; index < e.innerMap.length; index++) {
        e.innerMap[index].startTime = AppBace.filter(
          e.innerMap[index].startTime
        );
        e.innerMap[index].endTime = AppBace.filter(e.innerMap[index].endTime);
      }
      return JSON.stringify(e);
    },

    async submit() {
      //   this.experienceTemp = await this.init(this.experienceTemp.innerMap);
      // this.showover = true;
      var _this = this;
      this.formDate = new FormData();
      this.$refs.upload.submit();
      var gender = "";
      var roleId = "";
      switch (this.input.gender) {
        case "男":
          gender = 0;
          break;
        default:
          gender = 1;
          break;
      }
      switch (this.input.roleId) {
        case "运维人员":
          roleId = "2";
          break;
        case "环保管理部门":
          roleId = "4";
          break;
        case "水站管理员":
          roleId = "3";
          break;
        default:
          roleId = "1";
          break;
      }
      var experienceTemp = await this.init(this.experienceTemp);

      var url = "/register";
      if (
        this.input.phone == "" ||
        roleId == "" ||
        this.input.username == "" ||
        this.enterprise == ""
      ) {
        this.showover = false;
        Toast.fail("请完善信息");
      } else {
        this.formDate.append("birth", AppBace.filter(this.input.birth));
        this.formDate.append("gender", gender);
        this.formDate.append("identity", this.input.identity);
        this.formDate.append("phone", this.input.phone);
        this.formDate.append("profession", this.input.profession);
        this.formDate.append("roleId", roleId);
        this.formDate.append("school", this.input.school);
        this.formDate.append("username", this.input.username);
        this.formDate.append("technicalTitle", this.input.technicalTitle);
        this.formDate.append("enterprise", this.enterprise);
        this.formDate.append("experienceTemp", experienceTemp);
        var res = await AppBace.request(url, "POST", this.formDate);
        console.log(res);
        if (res.data.message === "success") {
          Toast("注册成功，正在审核");
          _this.showover = false;
          _this.$router.go(-1);
        } else {
          // this.$Toast.error("上传失败，请核对信息！");
          Toast.fail("失败，请重试");
          _this.showover = false;
        }
      }
      // console.log(res);
    }
  }
};
</script>
<style scoped>
.flexForget {
  display: flex;
