  border: none;
  outline: none;
  width: 100%;
  padding: 0 10px;
  /* text-align: center; */
  background: white;
  height: 40px;
}
.forgetContent {
  margin: 10px;
  background: white;
  padding: 15px;
  border-radius: 5px;
  color: #525151;
}
.forgetContent1 {
  margin: 10px;
  background: white;
  /* padding: 15px; */
  border-radius: 5px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background: #4ed995;
  height: 40px;
}
.mu-text-field-input {
  height: 36px;
  text-align: center;
  justify-content: center;
  display: flex;
  padding: 10px;
}
.header {
  position: fixed;
  top: 0;
}
#textarea {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  padding: 5px;
  outline: none;
}
.flexForget2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  align-items: center;
  /* border: 1px solid rgb(238, 238, 238); */
  margin-bottom: 15px;
  border-radius: 10px;
}
.add {
  /* text-align: center; */
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.borderAdd {
  border: #ccc solid 1px;
  padding: 5px;
  border-radius: 5px;
  background: white;
  color: #4ed995;
}
.bottom {
  position: absolute;
  top: -2px;
  right: -5px;
  /* left: 50%; */
}
.add img {
  width: 26px;
  height: 26px;
}
.input {
  border: 1px solid #ccc;
  border-radius: 10px;
  height: 36px;
}
.width {
  width: 80px;
}
.imageSize img {
  width: 20px;
  height: 20px;
}
</style>


